import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController } from 'ionic-angular';

import { Item } from '../../models/item';
import { Professionals } from '../../providers/professionals/professionals';
import { ProfessionalMapper } from '../../models/professional.model';
import { ProfessionalDetailPage } from '../index';
import { User } from '../../providers';
import { UserMapper } from '../../models/user.model';

@IonicPage()
@Component({
  selector: 'page-professionals',
  templateUrl: 'professionals.html'
})
export class ProfessionalsPage {

  searching: boolean = false;

  currentItems: ProfessionalMapper[];

  public loggedUser: UserMapper;

  constructor(public navCtrl: NavController, public professionals: Professionals,
              public user: User,
              public modalCtrl: ModalController) {
    this.searching = true;
    this.professionals.query().subscribe((data: ProfessionalMapper[]) => {
      this.searching = false;
      this.currentItems = data;
      console.log('this.professionalCurrentItems', this.currentItems);
    })
  }

  /**
   * The view loaded, let's query our items for the list
   */
  ionViewDidLoad() {
    this.getLoggedUser();
  }

  async getLoggedUser() {
    this.loggedUser = await this.user.user();
  }

  /**
   * Prompt the user to add a new item. This shows our ItemCreatePage in a
   * modal and then adds the new item to our data source if the user created one.
   */
  addItem() {
    let addModal = this.modalCtrl.create('ItemCreatePage');
    addModal.onDidDismiss(item => {
      if (item) {
        this.professionals.add(item);
      }
    });

    addModal.present();
  }

  /**
   * Delete an item from the list of items.
   */
  deleteItem(item) {
    this.professionals.delete(item);
  }

  /**
   * Navigate to the detail page for this item.
   */
  openItem(item: Item) {
    let modal = this.modalCtrl.create(ProfessionalDetailPage, {
      item: item
    });
    modal.present();
  }
}



